/* Join Room */
.join {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 400px;
  gap: 10px;
  padding: 40px 20px;
  border-radius: 10px;
  box-shadow: 0 2px 8px 2px rgb(164, 165, 168);
}

.join input,
select,
button {
  height: 45px;
  border: none;
  outline: none;
  color: rgb(59, 60, 62);
  border-radius: 4px;
  border: 1px solid rgb(182, 184, 190);
  font-size: 16px;
}

.join input::placeholder,
select::placeholder {
  color: hsl(223, 3%, 56%);
}

.join input,
select {
  padding: 0 10px;
}

.join button:hover {
  background-color: inherit;
}

.join button:active {
  background-color: rgb(201, 205, 220);
}

/* ChatNdParticipants */
.rightBox {
  border-radius: 10px;
  width: 30%;
  background-color: white;
  min-width: 250px;
  padding: 20px 15px;
}

.rightBox__optionView {
  height: 100%;
}

.rightBox__head {
  width: 100%;
  text-align: left;
  height: fit-content;
}

.rightBox__head .selected {
  color: black;
}

.rightBox__head span {
  font-weight: bold;
  color: #a6a8ab;
  cursor: pointer;
}

.rightBox__head span:nth-child(1) {
  margin-right: 10px;
}

.rightBox__chats {
  height: calc(100% - 60px);
  overflow: scroll;
  padding: 10px 0;
  ms-overflow-style: none;
  scrollbar-width: none; /* Firefox */
}

.rightBox__chats::-webkit-scrollbar {
  display: none;
}

.rightBox__optionView form input {
  border: none;
  width: 100%;
  height: 40px;
  background-color: hsl(228, 25%, 88%);
  border-radius: 10px;
  padding: 0 10px;
  box-sizing: border-box;
}

.rightBox__optionView form input:focus {
  outline: none;
}

.rightBox__participants {
  padding: 10px 0;
  height: 100%;
  overflow: scroll;
  ms-overflow-style: none;
  scrollbar-width: none; /* Firefox */
}

.rightBox__participants::-webkit-scrollbar {
  display: none;
}

.rightBox__participant {
  text-align: left;
  text-align: left;
  background-color: #e9ebef;
  padding: 10px;
  border-radius: 10px;
  margin-bottom: 10px;
}

.rightBox__participant p {
  font-size: 14px;
}

/* Controls */
.controls {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  padding: 20px 0;
  height: 80px;
  margin-top: 10px;
  background-color: white;
  border-radius: 10px;
  gap: 10px;
}

.controls .MuiButton-root .MuiSvgIcon-root {
  margin-right: 5px;
}

.controls .MuiButton-root img {
  height: 20px;
  width: 20px;
  margin-left: 5px;
}

.controls .MuiButton-root {
  text-transform: unset;
  padding: 10px;
  border-radius: 10px;
}

.controls .leave {
  background-color: rgb(241, 110, 110);
}

.controls .leave:hover {
  background-color: rgb(242, 87, 87);
}

/* HlsView and VideoTile */
video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
}

.video {
  width: calc(50% - 10px);
  height: unset;
}

/* Message */
.message {
  width: fit-content;
  text-align: left;
  max-width: 90%;
}

.message span {
  font-size: 12px;
  color: rgb(191, 196, 199);
}

.message p {
  background-color: hsl(228, 10%, 90%);
  padding: 5px 10px;
  border-radius: 10px;
  font-size: 14px;
  overflow: hidden;
  margin-bottom: 5px;
}

.myMessage {
  margin-left: auto;
  text-align: right;
}

.myMessage p {
  background-color: #1d84ea;
  color: white;
}

/* Room */
.room {
  display: flex;
  width: 95vw;
  height: 90vh;
  border-radius: 10px;
  gap: 10px;
}

.room__streamSpace {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.liveStream {
  border-radius: 10px;
  height: calc(100% - 90px);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  overflow: hidden;
}

/* Stream */
.stream {
  border-radius: 10px;
  /* height: calc(100% - 90px); */
  height: 510px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  flex-wrap: wrap;
  overflow: hidden;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.7);
}

.messageSend {
  position: relative;
}

.emotes {
  right: 6px;
  top: 6px;
  position: absolute;
  background-color: transparent;
  height: 25px;
  width: 25px;
  line-height: 0;
  cursor: pointer;
}

.emotes:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.emoteField {
  position: absolute;
  top: -50px;
  left: 0;
}
